@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
body {
  font-family: Roboto Mono;
  background-color: #2D3047;
  color: #FAF9F6;
}

button {
  font-family: inherit;
  text-align: center;
  font-weight: bold;
  border: none;
  cursor: pointer;
}

input, select, textarea {
  font-family: Roboto Mono;
  background: transparent;
  color: #FAF9F6;
  border: none;
  border-bottom: 2px solid #1B998B;
  outline: none;
}

#app {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#header {
  background-color: #1B998B;
  border-radius: 5px;
  margin: 20px;
  padding: 10px;
  text-align: center;
}

.addButton {
  border-radius: 5px;
  border: none;
  background-color: #FAF9F6;
}

.deleteButton {
  border-radius: 5px;
  border: none;
  background-color: #ED217C;
  color: white;
  margin-left: auto;
  width: auto;
}

.error {
  color: red;
}

.notification {
  color: greenyellow;
}

.person {
  background-color: #212233;
  display: flex;
  flex-direction: row;
  border-radius: 5px;
  margin: 0.5rem auto;
  padding: 0.5rem;
  padding-left: 0.5rem;
  width: 100%;
}

.search {
  margin: 0.5rem auto;
  width: 400px;
  display: flex;
}

.searchBar {
  width: 100%;
  margin-left: 0.5rem;
}

.formRow {
  margin: 5px auto;
}

.formInput {
  background-color: #ffffff;
  color: #000000;
  border: 1px solid #99828c;
  border-radius: 3px;
  padding: 0.25rem;
  font-size: 1rem;
  width: 100%; 
}

.personsList {
  width: 400px;
}
